.teaching{
    display: flex;
    width: 100vw;
    margin-top: 8vh;
    background: whitesmoke;
    label,input{
        left: 20vw;
        position: fixed;
        transition:0.5s;
    }
    label{
        border-color: transparent transparent transparent rgba(45,52,54,0.2);
        border-style: solid solid solid solid;
        border-width: 5vmin;
        height: 0px;
        width: 0px;
    }
    label:hover{
        cursor: pointer;
        border-color: transparent transparent transparent rgba(45,52,54,0.6);
    }
    input{
        display: none;
    }
    input:checked ~ .teach_list{
        left: -20vw;
        transition:0.5s;
    }
    input:checked ~ .teach_article{
        width: 100vw;
        margin-left: 0;
        transition:0.5s;
    }
    input:checked + label{
        left: 0vw;
        transition:0.5s;
    }
    .teach_list{
        box-shadow: 1px 1px 10px black;
        left: 0vw;
        transition:0.5s;
        width: 20vw;
        height: 92vh;
        position: fixed;
        background: rgba(45,52,54,0.2);
        z-index: 2;
        ul{
            width: inherit;
            padding: 0;
            margin: 0;
            text-align: start;
            li{
                font-size: 4vh;
                width: inherit;
                list-style: none;
            }
        }
        .detail{
            text-align: center;
            li{
                font-size: 2.5vh;
                padding: 0;
            }
        }
    }
    .teach_article{
        width: 80vw;
        margin-left: 20vw;
        padding-left: 5%;
        padding-right: 10%;
        padding-bottom: 5%;
        transition:0.5s;
        line-height: 3vmax;
    }
}