.about{
    width: 100vw;
    height: 92vh;
    margin-top: 8vh;
    display: flex;
    align-items: center;
    background: whitesmoke;
    div{
        width: 32vw;
        height: 85vh;
        background: white;
        margin-left: 1vw;
    }
    .me{
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        .pic{
            background: gray;
            width: 10vw;
            height: 10vw;
            margin-top: 1vw;
            border-radius: 50%;
        }
        .info{
            width: 100%;
            height: 70%;
            text-align: center;
            margin: 0;
            padding:0 5vw;
        }
    }
    .skill{
        display: flex;
        flex-wrap: wrap;
        .skill_title{
            width: 100%;
            height: auto;
        }
        .main{
            height: auto;
            ul{
                li{
                    margin: 1vw;
                }
            }
        }
    }
    .contact{
        display: flex;
        flex-wrap: wrap;
        .contact_title{
            width: 100%;
            height: auto;
        }
    }
}